<template>
  <div class="coupons">
    <div class="coupon" v-for="(item, idx) in couponList" :key="idx" :class="{active: acIdx === idx}"
         @click="chooseCoupon(idx)">
      <div class="coupon-left">
        <i class="circle"></i>
        <p class="price">￥<i>{{item.price}}</i></p>
      </div>
      <div class="coupon-right">
        <p class="title">{{item.name}}</p>
        <p class="goodsType">全部商品可用</p>
        <p>订单满{{item.full}}减{{item.decrease}}元</p>
        <p>{{item.beginDate}} - {{item.endDate}}</p>
      </div>
      <i class="tag" :class="{over: item.tip === 0, recent: item.tip === 1, ing: item.tip === 2}">
        {{item.tip === 0 ? '已过期': item.tip === 1 ? '将过期' : '可用中'}}
      </i>
    </div>
  </div>
</template>

<script>
    import {formatTime, figureUsage} from '@/utils/validate'

    export default {
        name: "coupons",
        data() {
            return {
                acIdx: 0,
                couponList: [
                    {
                        id: '1232',
                        name: '中秋优惠券',
                        price: '20',
                        full: 300,
                        decrease: 20,
                        beginDate: formatTime(new Date()),
                        endDate: formatTime(new Date())
                    },
                    {
                        id: '1232',
                        name: '中秋优惠券',
                        price: '20',
                        full: 300,
                        decrease: 20,
                        beginDate: formatTime(new Date()),
                        endDate: formatTime(new Date())
                    },
                    {
                        id: '1232',
                        name: '中秋优惠券',
                        price: '20',
                        full: 300,
                        decrease: 20,
                        beginDate: formatTime(new Date()),
                        endDate: formatTime(new Date())
                    },
                    {
                        id: '1232',
                        name: '中秋优惠券',
                        price: '20',
                        full: 300,
                        decrease: 20,
                        beginDate: formatTime(new Date()),
                        endDate: formatTime(new Date())
                    },
                    {
                        id: '1232',
                        name: '中秋优惠券',
                        price: '20',
                        full: 300,
                        decrease: 20,
                        beginDate: formatTime(new Date()),
                        endDate: formatTime(new Date())
                    }
                ]

            }
        },
        created() {
            this.$parent.isCloseText = true
            this.$parent.closeText = '确定关闭界面嘛？'
            this.couponList.forEach(item => {
                item.tip = figureUsage(item.endDate)
            })
        },
        methods: {
            chooseCoupon(idx) {
                this.acIdx = idx
            }
        }
    }
</script>

<style lang="scss" scoped>
  @import "~@/style/common/variables.scss";

  .coupons {
    max-height: 620px;
    overflow-y: auto;

    .coupon {
      cursor: pointer;
      border: 1px solid $border;
      display: flex;
      padding: 10px 10px;
      margin-bottom: 20px;
      position: relative;

      &:last-child {
        margin-bottom: 0;
      }

      &.active {
        .coupon-left {
          .circle {
            background-color: $menuActiveText;
          }
        }
      }

      .coupon-left {
        margin-right: 10px;

        .circle {
          width: 15px;
          height: 15px;
          display: block;
          margin: 5px auto;
          border-radius: 50%;
          background-color: $menuText;
        }

        .price {
          line-height: 40px;
          padding: 10px 10px;
          font-size: 24px;
          color: $menuActiveText;
        }
      }

      .coupon-right {
        flex: 1;
        padding-right: 50px;
        line-height: 20px;
        color: $menuText;

        .title {
          color: #000000;
          font-weight: bold;
        }

        .goodsType {
          margin-bottom: 5px;
        }
      }

      .tag {
        position: absolute;
        top: 0;
        right: 0;
        padding: 10px 10px;
        color: #ffffff;

        &.over {
          background-color: $goodsOver;
        }

        &.recent {
          background-color: $goodsRecent;
        }

        &.ing {
          background-color: $goodsIng;
        }
      }
    }
  }
</style>



