<template>
  <div class="shortcutKey">
    <div class="usualKeys" :class="{marginRight: isExpand}">
      <div class="item" v-for="(item) in keys" :key="item.key" @click="quickKey(item.key, item.clipboard)">
        <span>{{item.name}}</span>
        <span>【{{item.key}}】</span>
      </div>
    </div>
    <div class="expendKeys" v-if="isExpand">
      <div class="item" v-for="(item) in anotherKeys" :key="item.key" @click="quickKey(item.key, item.clipboard)">
        <span>{{item.name}}</span>
        <span>【{{item.key}}】</span>
      </div>
    </div>
    <span class="tag" @click="changeExpand">
      <svg-icon :icon-class="isExpand ? 'packup' : 'expand'"></svg-icon>
    </span>

    <el-dialog
      modal-append-to-body
      append-to-body
      lock-scroll
      :before-close="handleClose"
      :visible.sync="dialogTableVisible"
    >
      <!-- 弹窗内容开始 搭配codeA和codeA的值选择展示哪种弹框 -->
      <coupons v-if="codeA && codeB === 112"></coupons>
      <!-- 弹窗内容结束 -->
    </el-dialog>
  </div>
</template>

<script>
    import coupons from "@/components/coupons"

    const _KEYS = [{name: '删单', key: 'Alt+F1', clipboard: 112}, {name: '挂单', key: 'Alt+F2', clipboard: 113}, {
        name: '提取挂单',
        key: 'Alt+F3',
        clipboard: 114
    },
        {name: '原单退货', key: 'Alt+F4', clipboard: 115}, {name: '办理会员', key: 'Alt+F5', clipboard: 116}, {
            name: '积分兑换',
            key: 'Alt+F6',
            clipboard: 117
        },
        {name: '重打小票', key: 'Alt+F7', clipboard: 118}, {name: '药方调配', key: 'Alt+F8', clipboard: 119}, {
            name: '对账',
            key: 'Alt+F9',
            clipboard: 120
        },
        {name: '交班', key: 'Alt+F10', clipboard: 121}, {name: '选择优惠券', key: 'Alt+F11', clipboard: 122}, {
            name: '计算器',
            key: 'Alt+F12',
            clipboard: 123
        },
        {name: '处方记录', key: 'Alt+Q', clipboard: 81}, {name: '含麻记录', key: 'Alt+W', clipboard: 87}, {
            name: '药方记录',
            key: 'Alt+E',
            clipboard: 69
        }
    ]
    const _ANOTHERKEYS = [{name: '系统设置', key: 'Alt+R', clipboard: 82}]

    export default {
        name: "shortcutKey",
        components: {coupons},
        data() {
            return {
                keys: _KEYS,
                anotherKeys: _ANOTHERKEYS,
                isExpand: false,
                dialogTableVisible: false,
                codeA: false,
                codeB: 0,
                isCloseText: false,
                closeText: ''
            }
        },
        watch: {
            dialogTableVisible(val) {
                if (!val) {
                    this.codeA = false
                    this.codeB = 0
                    window.addEventListener('keydown', this.focusKeydown, false)
                    window.addEventListener('keyup', this.blurKeyup, false)
                }
            }
        },
        created() {
            window.addEventListener('keydown', this.focusKeydown, false)
            window.addEventListener('keyup', this.blurKeyup, false)
        },
        methods: {
            /**
             * 快捷键触发弹窗展示
             */
            quickKey(key, num) {
                this.codeA = true
                this.codeB = num
                this.changeDialogStatus()
            },
            changeExpand() {
                this.isExpand = !this.isExpand
            },
            changeDialogStatus() {
                this.dialogTableVisible = !this.dialogTableVisible
                if (this.dialogTableVisible) {
                    window.removeEventListener('keydown', this.focusKeydown, false)
                    window.removeEventListener('keyup', this.blurKeyup, false)
                }
            },
            focusKeydown(e) {
                console.log('codeA:', this.codeA, 'codeB:', this.codeB);
                let key = window.event.keyCode;
                // e.cancelBubble = true;
                // e.returnValue = false;
                // if (e.preventDefault) e.preventDefault();
                // if (e.stopPropagation) e.stopPropagation();
                if (key === 18) {
                    this.codeA = true
                } else {
                    this.codeB = key
                }
                if (this.codeA && _KEYS.some(item => item.clipboard === this.codeB)) {
                    this.dialogTableVisible = true
                }
            },
            blurKeyup(e) {
                let key = window.event.keyCode;
                console.log(key, 'key')
                if (this.dialogTableVisible) {
                    window.removeEventListener('keydown', this.focusKeydown, false)
                    window.removeEventListener('keyup', this.blurKeyup, false)
                } else {
                    if (key === 18) {
                        this.codeA = false
                    } else {
                        this.codeB = 0
                    }
                }
            },
            handleClose(done) {
                if (this.isCloseText) {
                    this.$confirm(this.closeText)
                        .then(_ => {
                            done()
                        })
                        .catch(_ => {
                        })
                } else {
                    done()
                }

            }
        }
    }
</script>

<style lang="scss" scoped>
  @import "~@/style/common/variables.scss";

  .shortcutKey {
    display: flex;
    /*padding: 0 10px;*/
    position: relative;
    background-color: #ffffff;

    span {
      display: block;
    }

    .usualKeys, .expendKeys {
      display: flex;
      flex-wrap: wrap;
      max-width: 580px;

      .item {
        cursor: pointer;
        color: $menuText;
        width: 115px;
        padding: 0 15px;
        text-align: center;
        line-height: 30px;
        max-height: 60px;

        &:hover {
          border-radius: 10px;
          background-color: $menuActiveText;
          color: #ffffff;
        }
      }
    }

    .usualKeys {
      &.marginRight {
        margin-right: 10px;
      }
    }

    .expendKeys {
      flex-direction: column;
      max-height: 180px;
      flex-wrap: wrap;
    }

    .tag {
      -moz-user-select: none;
      -webkit-user-select: none;
      user-select: none;
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      padding: 6px 5px 6px 15px;
    }
  }
</style>



